<template>
  <AppLayout>
    <ViewWrapper
      title="profile"
      :tabs="tabs"
      :breadcrumbs="breadcrumbs"
      @quickHelp="showQuickHelp"
    >
      <!-- header -->

      <template #header-actions>
        <BaseButton
          v-if="showAddProfile"
          icon="eva-plus"
          label="add profile"
          class="q-ml-md"
          @click="addUser"
        />
      </template>

      <!-- ... -->

      <!-- quick help -->

      <QuickHelp v-model="quickHelp" />

      <!-- ... -->

      <!-- child routes -->

      <router-view :key="$route.fullPath" />

      <!-- ... -->
    </ViewWrapper>
  </AppLayout>
</template>

<script>
import AppLayout from "@/layouts/app/AppLayout.vue";
import ViewWrapper from "@/components/common/view-wrapper/ViewWrapper.vue";
import QuickHelp from "@/components/common/QuickHelp.vue";

export default {
  name: "Profile",

  components: { AppLayout, ViewWrapper, QuickHelp },

  data() {
    return {
      breadcrumbs: [
        {
          id: this.$nano.id(),
          label: "dashboard",
          route: "dashboard",
        },
        {
          id: this.$nano.id(),
          label: "settings",
          route: "settings",
        },
        {
          id: this.$nano.id(),
          label: "profile",
          route: "settings",
        },
        {
          id: this.$nano.id(),
          label: "",
          route: "",
        },
      ],
      tabs: [
        // {
        //   id: this.$nano.id(),
        //   label: "overview",
        //   icon: "mdi-view-dashboard-outline",
        //   route: "profile-overview",
        // },
        {
          id: this.$nano.id(),
          label: "browse",
          icon: "eva-folder-outline",
          route: "profile-browse",
        },
        {
          id: this.$nano.id(),
          label: "trash",
          icon: "eva-trash-2-outline",
          route: "profile-trash",
        },
      ],
      quickHelp: false,
    };
  },

  computed: {
    showAddProfile() {
      if (this.$store.state.profileMenus.length)
        return this.$store.state.profileMenus.find((row) => row.settings).Menu[
          "Manage Profiles"
        ];

      return false;
    },
  },

  watch: {
    $route: {
      immediate: true,
      deep: true,
      handler() {
        const index = this.breadcrumbs.length - 1;
        this.breadcrumbs[index].label = this.$route.meta.breadcrumb;
      },
    },
  },

  methods: {
    addUser() {
      this.$router.push({ name: "profile-builder" });
    },

    showQuickHelp() {
      this.quickHelp = true;
    },
  },
};
</script>

<style lang="scss" scoped></style>
